.pagination {
    display: flex;
    position: fixed;
    bottom: 37vh;
  }
  ul {
    list-style: none;
    padding: 0;
  }
  ul.pagination li {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  }
  ul.pagination li:first-child{
    border-radius: 5px 0 0 5px;
  }
  ul.pagination li:last-child{
    border-radius: 0 5px 5px 0;
  }
  ul.pagination li a {
    text-decoration: none;
    color: white;
    font-size: 1rem;
  }
  ul.pagination li.active a {
    color: white;
  }
  ul.pagination li.active {
    background-color: yellowgreen;
    border-radius: 50%;
  }
  ul.pagination li a:hover,
  ul.pagination li a.active {
    color: blue;
  }
  .page-selection {
    width: 48px;
    height: 30px;
    color: #337ab7;
  }